import { Button } from '@material-ui/core'
import React from 'react'

function MuiButton(props) {
    return (
        <div>
            <Button
                style={{ margin: '0', padding: '0' }}
                onClick={props.clicked}
                variant={props.variant}
                color={props.color}
                size={props.size}>
                {props.children}
            </Button>
        </div>
    )
}

export default MuiButton
